import '../styles/css/info.css'

export default function Info() {

    function ExitButton() {
        localStorage.setItem('login', '');
        window.location.reload();
    }

    return(
        <div id='me-info' className="me-info">
            <img src={require('../images/user.png')} alt="me-photo" />
            <div className="me-name">
                <h2>{localStorage.getItem('login')}</h2>
                <button onClick={ExitButton}>EXIT <img src={require('../images/logout.png')} alt="exit" /></button>
            </div>

            <div className="line"></div>

            <div className="follows">
                <div className="total info-hu">
                    <h3>86 NFT</h3>
                    <p className="description">Total NFT</p>
                </div>
                <div className="followers info-hu">
                    <h3>12K</h3>
                    <p className="description">Followers</p>
                </div>
                <div className="total info-hu">
                    <h3>582</h3>
                    <p className="description">Followings</p>
                </div>
            </div>

            <div className="bio">
                <h4>Bio</h4>
                <p>
                    Lorem ipsum, dolor sit amet consectetur adipisicing elit. Minima adipisci reprehenderit atque voluptate in accusantium, a error quam culpa, odit blanditiis accusamus excepturi facilis laboriosam!
                </p>
            </div>
        </div>
    )
}