
import '../styles/css/modalTemplate.css';

interface ModalInterface {
    children: React.ReactNode
    onClose: () => void
}

export default function ModalTemplate({children, onClose}: ModalInterface) {
    return (
        <>
        <div className="modal-template" onClick={() => onClose()}> </div>
            <div className="content">
                {children}      
            </div>
        </>
    )
}