import { NavLink } from 'react-router-dom';
import '../styles/css/main.css';

export default function Main() {
    return (
        <div className="head">
            <div className="left">
                <div className="flex-line"><div className="line"></div><p>OVER 1M CREATORS</p></div>
                <h1>Discover And Create NFTs</h1>
                <p className="descript">
                    Discover, Create and Sell NFTs On Our NFT Marketplace With Over Thousands Of NFTs And Get a <span className="bold">$20 bonus</span>.
                </p>
                <div className="main-btns">
                    <NavLink  onClick={() => window.scrollTo(0, 0)} to='/discover' className="explore btn">EXPLORE MORE</NavLink>
                    <NavLink  onClick={() => window.scrollTo(0, 0)} to='/sell' className="create btn">CREATE NFT</NavLink>
                </div> 
                <div className="footer-head">
                    <div className="art-works  elem-f">
                        <h3>430K+</h3>
                        <p className="descript-head">Art Works</p>
                    </div>
                    <div className="creators elem-f">
                        <h3>159K+</h3>
                        <p className="descript-head">Creators</p>
                    </div>
                    <div className="collections  elem-f">
                        <h3>87K+</h3>
                        <p className="descript-head">Collections</p>
                    </div>
                </div>
            </div>
            <div className="right">
                <div className="col-1">
                    <img src={require("../images/pict-1.png")} alt="main" />       
                </div>
                <div className="col-2">
                    <div className="top">
                        <img src={require("../images/Arrow.png")} alt="arrow" className="spin-arrow" />
                        <img src={require("../images/dotted.png")} alt="dotted"  className="dotted"/>
                    </div>  
                    <img src={require("../images/pict-2.png")} alt="blur" className="blur" />
                </div>
            </div>
        </div>
    )
}