import React, {useState} from 'react';
import '../styles/css/questions.css'

export default function Questions() {
    const [display1, setDisplay1] = useState(false);
    const [display2, setDisplay2] = useState(false);
    const [display3, setDisplay3] = useState(false);
    const [display4, setDisplay4] = useState(false);
    return( 
        <article className="questions">
            <h2 className="top-h2">Frequently Asked </h2>
            <h2 className="bottom-h2">Questions</h2>
            <table>
                <tr className='button' onClick={() => setDisplay1(prev => !prev)}>
                    <p>What is an NFT?</p> 
                    <img src={require('../images/plus.png')} alt="plus" />
                </tr>
                {display1 && <tr className='hide'>
                    <p>
                        NFT stands for Non-Fungible Token. It is a type of digital asset that represents ownership or proof of authenticity of a unique item or piece of content, such as artwork, music, videos, collectibles, and more, using blockchain technology.
                    </p>
                </tr>}

                <tr className='button' onClick={() => setDisplay2(prev => !prev)}>
                    <p>What can I use NFTs for?</p> 
                    <img src={require('../images/plus.png')} alt="plus" />
                </tr>
                {display2 && <tr className='hide'>
                    <p>
                    NFTs can be used for Art and Collectibles: NFTs have become particularly popular in the art world, allowing artists to tokenize and sell their digital artworks as unique pieces. This provides a way for artists to monetize their work and for collectors to own and trade digital art.
                    </p>
                </tr>}

                <tr className='button' onClick={() => setDisplay3(prev => !prev)}>
                    <p>What is the difference between an NFT and cryptocurrency?</p> 
                    <img src={require('../images/plus.png')} alt="plus" />
                </tr>
                {display3 && <tr className='hide'>
                    <p>
                        The main difference between an NFT and cryptocurrency is their fungibility. Cryptocurrencies, such as Bitcoin or Ethereum, are fungible, meaning that each unit is interchangeable and holds the same value as any other unit of the same cryptocurrency. For example, one Bitcoin is equal in value to any other Bitcoin. On the other hand, NFTs are non-fungible, which means that each token is unique and cannot be exchanged on a one-to-one basis with another token. Each NFT has distinct characteristics, properties, and metadata that make it different from any other NFT. This uniqueness is what gives NFTs their value and allows them to represent ownership of a specific digital asset.
                    </p>
                </tr>}

                <tr className='button' onClick={() => setDisplay4(prev => !prev)}>
                    <p>How much is an NFT worth?</p> 
                    <img src={require('../images/plus.png')} alt="plus" />
                </tr>
                {display4 && <tr className='hide'>
                    <p>
                        The value of an NFT can vary greatly and is ultimately determined by supply and demand in the market. Some NFTs have sold for millions of dollars, while others may have little to no value.
                    </p>
                </tr>}
                
            </table>
        </article>
    ) 
}