import { useState } from "react"

interface interfaceAboutProp {
    creator: {
        name: string,
        followers: number,
        items: number,
        floor: number,
        traded: number,
        photo1: string,
        photo2: string,
        photo3: string,
        backPhoto: string,
        id: number,
        bio: string
    } | undefined
}
export default function CreatorInfo({creator}: interfaceAboutProp) {
    const [follow, setFollow] = useState(false)

    return (
        <div id="me-info" className="me-info creator-info">
        <img src={require('../../'+creator?.photo1)} alt="me-photo" />
        <div className="me-name">
            <h2>{creator?.name}</h2>
            {!follow && <button onClick={() => setFollow(prev => !prev)}>FOLLOW +</button>}
            {follow && <button onClick={() => setFollow(prev => !prev)} className="following">UNFOLLOW -</button>}
        </div>

        <div className="line"></div>

        <div className="follows">
            <div className="total info-hu">
                <h3>{creator?.items}</h3>
                <p className="description">Items</p>
            </div>
            <div className="total info-hu">
                <h3>{creator?.followers}</h3>
                <p className="description">Followers</p>
            </div>
            <div className="total info-hu">
                <h3>{creator?.traded}</h3>
                <p className="description">Traded</p>
            </div>
        </div>

        <div className="bio">
            <h4>Bio</h4>
            <p>
                {creator?.bio}
            </p>
        </div>
    </div>
    )
}