import './styles/css/createNFT.css';
import FilePreviewer from './ChooseNFT';
import {useForm, FormProvider} from 'react-hook-form';


interface IFormInputs {
    name: string;
    description: string;
    royalty: string;
    size: string;
    price: string;
    tags: string;
    'price-select': string;
    'stock-select': string;
    toggle1: string;
    toggle2: string;
}
export default function CreateNFT() {
    const methods = useForm<IFormInputs>();
    const {
        register,
        formState: {
            errors
        },
        handleSubmit
    } = methods;
    
    function onSubmit(data: {}) {
        // Здесь должна происходить отправка массива данных на сервер
        // Например: axios.post('https://ссылка', data)
        // Но у меня нет такой возможности
    }

    return(
        <article id='createNFT' className="createNFT">
            <h2>Create Your NFT</h2>
            <FormProvider {...methods} > 
            <form onSubmit={handleSubmit(onSubmit)} action="">
                <div className="inputs-col1">
                    <div className="input">
                        <h3>Name</h3>
                        <input 
                        {...register('name', {
                            required: 'Input field must not be empty!',
                            minLength: {
                                value: 5,
                                message: 'Minimum of 5 characters'
                            },
                            maxLength: {
                                value: 30,
                                message: 'Maximum of 30 characters'
                            }
                            })} autoComplete='off' type="text"  placeholder="ArtWork Name" id="name" />
                        {errors?.name && 
                        <div className='error'> 
                            <img src={require('./images/cross.png')} alt="" />
                            <p>{errors?.name?.message}</p>
                        </div>}
                    </div>

                    <div className="input">
                        <h3>Description</h3>
                        <textarea {...register('description', 
                        {
                            required: 'Input field must not be empty!',
                            minLength: {
                                value: 30,
                                message: 'Minimum of 30 characters'
                            },
                            })} placeholder="Enter Your Description" id="description" />
                        {errors?.description && 
                        <div className='error'> 
                            <img src={require('./images/cross.png')} alt="" />
                            <p>{errors?.description?.message}</p>
                        </div>}
                    </div>

                    <div className="royal-size">
                        <div className="input">
                            <h3>Royalty</h3>
                            <select {...register('royalty')} id="royalty">
                                <option selected value="not">Not Royalty</option>
                                <option value="5">5% for 10 years</option>
                                <option value="10">10% for 5 years </option>
                                <option value="15">15% for 3 years</option>
                                <option value="20">20% for 1 years</option>
                            </select>
                        </div>

                        <div className="input">
                            <h3>Size</h3>
                            <input {...register('size', {required: 'Input field must not be empty!',
                            minLength: {
                                value: 5,
                                message: 'The record should be in the format 100 x 100'
                            },})} autoComplete='off' type="text" placeholder="Ex - 100 x 100" id="size" />
                            {errors?.size && 
                        <div className='error'> 
                            <img src={require('./images/cross.png')} alt="" />
                            <p>{errors?.size?.message}</p>
                        </div>}
                        </div>
                    </div>

                    <div className="input">
                        <h3>Tags</h3>
                        <input {...register('tags', {
                            required: 'Input field must not be empty!',
                            minLength: {
                                value: 10,
                                message: 'Minimum of 10 characters'
                            }
                    })} autoComplete='off' type="text" placeholder="Beautiful Castle, Monkeys ETC" id="tags" />
                        {errors?.tags && 
                        <div className='error'> 
                            <img src={require('./images/cross.png')} alt="" />
                            <p>{errors?.tags?.message}</p>
                        </div>}
                    </div>

                    <div className="price-stock">
                        <div className="input">
                            <h3>Price</h3>
                            <select {...register("price-select")} id="price-select">
                                    <option value="bth">BTH</option>
                                    <option value="eth">ETH</option>
                                    <option value="bnb">BNB</option>
                                    <option value="ltc">LTC</option>
                                    <option value="ern">ERN</option>
                            </select>
                            <input  {...register("price", {required: 'Input field must not be empty!'})}  step="0.0000001" min="0.0000001" autoComplete='off' type="number" placeholder= "0.00007 ETH" id="price" />
                            {errors?.price && 
                        <div className='error'> 
                            <img src={require('./images/cross.png')} alt="" />
                            <p>{errors?.price?.message}</p>
                        </div>}
                        </div>
                        <div className="input">
                            <h3>Stock</h3>
                            <select {...register("stock-select")} id="stock-select">
                                    <option value="1">001</option>
                                    <option value="2">002</option>
                                    <option value="3">003</option>
                                    <option value="4">004</option>
                                    <option value="5">005</option>
                            </select>
                        </div>
                    </div>
                    <div className="toggles">
                    <div className="toogle">
                            <div className="desc">
                                <h3> Put On Sale</h3>
                                <p>People Will Bids On Your NFT Project</p>
                            </div>
                            <div className="switch">
                                <input  {...register("toggle1")} type="checkbox" id="switch1" />
                                <label htmlFor='switch1'>Toggle</label>
                            </div>
                            
                        </div>

                        <div className="toogle">
                            <div className="desc">
                                <h3> Direct Sale</h3>
                                <p>No Bids - Only Direct Salling</p>
                            </div>
                            <div className="switch">
                                <input {...register("toggle2")} type="checkbox" id="switch2" />
                                <label htmlFor='switch2'>Toggle</label>
                            </div>
                        </div>
                    </div>
                        
                        <button type='submit'>Create</button>
                </div>
                <div className="inputs-col2">
                <FilePreviewer />
                    
                </div>
                
            </form>
            </FormProvider>
        </article>
    )
}