import React, { useState} from 'react'
import '../styles/css/loginForm.css'

interface CreateInterface {
    onCreteLogin: (prop: {login: string, password: string}) => void
}

export const valueUserData = {
    login: '',
    password: ''
};

export default function LoginForm({onCreteLogin}: CreateInterface) {

    let [value1, setValue1] = useState('');
    let [value2, setValue2] = useState('');
    let [error, setError] = useState('');

    const HandleSubmit = (event: React.FormEvent) => {
        event.preventDefault();
        setError('');

        if (value1.trim().length === 0 && value2.trim().length !== 0) {
            setError('Enter the correct login!') ;
            return;
        }
        else if (value1.trim().length > 20 && value2.trim().length !== 0) {
            setError('The maximum login length is 20 characters!') ;
            return;
        }
        else if (value1.trim().length < 3 && value2.trim().length !== 0) {
            setError('The minimum login length is 3 characters!') ;
            return;
        }
        else if (value1.trim().length !== 0 && value2.trim().length === 0) {
            setError('Enter the correct password!');
            return;
        }
        else if (value1.trim().length !== 0 && value2.trim().length < 8) {
            setError('The minimum password length is 8 characters!');
            return;
        }
        else if (value1.trim().length === 0 && value2.trim().length === 0) {
            setError('Enter the correct variable!');
            return
        } else {
            valueUserData.login = value1;
            valueUserData.password = value2;  
        }
        onCreteLogin(valueUserData) 
       
        
    }
    return (
        <div className="modal">
            <h2>Join The NFT Community!</h2>
            <form onSubmit={HandleSubmit} className="form-modal">
                <input value={value1} onChange = {(event) => setValue1(event.target.value)} type="text" placeholder="enter your username... (max 15 characters)" />
                <input value={value2} onChange = {(event) => setValue2(event.target.value)} type="password" placeholder="enter your password... (min 8 characters)" />
                <p className="error">{error}</p>
                <button type="submit"> SIGN UP!</button>
            </form>  
        </div>
    )
    
}