
import { NavLink } from 'react-router-dom';
import '../styles/css/header.css';
import { useState } from 'react';

export interface openModal {
    onOpen: () => void
    login: boolean
};
function Header({onOpen, login}: openModal) {
    const [navHidden, setNavHidden] = useState(false)
    function toClose() {
        let el = document.getElementById('open')! as HTMLInputElement
        el.checked = false;
        setNavHidden(false)
    }
    return (
        <>
        <header id='header-nav'>
            <nav>
                <NavLink to="/"><img src={require("../images/Logo.png")} alt="logo" /></NavLink>
                <div className="nav-menu">
                    <NavLink className="nav-el" to="/discover">DISCOVER</NavLink>
                    <NavLink className="nav-el"to="/creators">CREATORS</NavLink> 
                    <NavLink className="nav-el" to="/sell">SELL</NavLink>
                    <NavLink className="nav-el" to="/stats">STATS</NavLink>
                </div>
                <input type="text" placeholder="Search Art Work / Creators"/>
                <div className="open-nav" >
                <input id='open' type='checkbox' onClick={() => setNavHidden(prev => !prev)}/>
                <label htmlFor="open">
                    <div className="btn-line line1"></div>
                    <div className="btn-line line2"></div>
                    <div className="btn-line line3"></div>
                </label>
                </div>
                {navHidden && <>
                <div className="back" onClick={() => toClose()}></div>
                    <nav className="menu-hidden">
                        <div className="nav-menu-hidden">
                            <NavLink className="nav-el" to="/discover">DISCOVER</NavLink>
                            <NavLink className="nav-el"to="/creators">CREATORS</NavLink> 
                            <NavLink className="nav-el" to="/sell">SELL</NavLink>
                            <NavLink className="nav-el" to="/stats">STATS</NavLink>
                        </div>
                        <input type="text" placeholder="Search Art Work / Creators"/>
                        {!login && <button onClick={() => onOpen()} className='sign'>SIGN UP!</button>} {/* по нажатию появляется модальное окно регистрации и если введенные данные правильные коннект валлет заменяется на картинку и имя логина*/ }
                        {login && 
                        <NavLink id='login' className="login" to="/me">
                            <img src={require("../images/user.png")} alt="user" />
                            <p>{localStorage.getItem('login')}</p>
                        </NavLink>}
                    
                    </nav>
                </>}
                {!login && <button onClick={() => onOpen()} className='sign'>SIGN UP!</button>} {/* по нажатию появляется модальное окно регистрации и если введенные данные правильные коннект валлет заменяется на картинку и имя логина*/ }
                {login && <NavLink className="login" to="/me">
                    <img src={require("../images/user.png")} alt="user" />
                    <p>{localStorage.getItem('login')}</p>
                </NavLink>}
            </nav>
        </header>
        
        </>
    )
}
export default Header;