import { slideInfo } from "../../arrays/slides"
import { NavLink } from "react-router-dom"

export default function CreatorCollections() {
    const newSlideInfo = slideInfo.slice(0, 8)
    return(
        <div id="me-collection" className="me-collection">
            <div className="header">
                <img src={require('../images/ticket.png')} alt="ticket" />
                <p>Collection</p>
            </div>
            <div className="collection-list">
            {newSlideInfo.map(slide => {
                        return (
                            <div className="slide" id={slide.id+''}>
                                <img src={require(`../../${slide.img}`)} alt="NFT" />
                                <h3>{slide.name}</h3>
                                <div className="slide-footer">
                                    <div className="left">
                                        <p className='current'>Current bid</p>
                                        <div className="price-flex">
                                            <img src={require('../images/ethereum.png')} alt="ethereum" />
                                            <p className='price'>{slide.price}</p>
                                        </div>
                                        
                                    </div>
                                    <NavLink onClick={() => window.scrollTo(0, 100)} to={'/card/' + slide.id}>PLACE BID</NavLink>
                                </div>
                            </div>
                            )
                        }
                    )
            }
            </div>
        </div>
    )
}