import creatorsList from '../arrays/creators.js';
import { useParams } from "react-router-dom";
import CreatorInfo from './components/CreatorInfo';
import CreatorCollections from './components/CreatorCollections';
import './styles/css/creatorProfile.css'

export default function CreatorProfile() {
    const param = Number(useParams().idCreator)
    const creator = creatorsList.find(x => x.id === param)

    return (
        <article className="mePage">
            <div>
                <img src={require('../'+creator?.backPhoto)} alt="bg" className="bg" />
            </div>
            <div className="content">
                <CreatorInfo creator={creator} />
                <CreatorCollections />
            </div>
        </article>
    )
}