import { NavLink } from 'react-router-dom';
import '../styles/css/createBlock.css';

export default function CreateBlock() {
    return (
        <article className="createBlock">
            <div id='cart' className="cart">
                <div className="info">
                    <h2>Create and Sell NFTs</h2>
                    <p>World’s Largest NFT Place</p>
                    <div className="btns-bottom">
                        <NavLink  onClick={() => window.scrollTo(0, 0)} to='/discover' className="btn btn-1">Explore More</NavLink>
                        <NavLink onClick={() => window.scrollTo(0, 0)} to='/sell' className="btn btn-2">Sell Artworks</NavLink>                  
                    </div>
                </div>
                <img src={require('../images/bottom-cart-png.png')} alt="" />
            </div>
        </article>
    )
}