import '../styles/css/weekly.css';
import { slideInfo } from "../../arrays/slides";
import { useState } from 'react';
import { NavLink } from 'react-router-dom';

export default function Weekly() {

    let slideInfoNew = slideInfo.slice(0, 10);

    let [position, setPosition] = useState(0);

    function btnLeft() {
        if (position !== 0 ) {
           position--;
           setPosition(position)
        } else { position = slideInfoNew.length-5; setPosition(position) }
        console.log(slideInfoNew.length);
        
    };

    function btnRight() {
        if (position !== slideInfoNew.length-5 ) {
            position++;
            setPosition(position)
        } else { position = 0; setPosition(position)}
        console.log(position);
    };

    return (
        <article>
            <h2>Weekly - Top NFT</h2>
            <div className="slider">
                <div className="slides" style={{position: 'relative', left: -(position*(276+50))}}>
                    {slideInfoNew.map(slide => {
                        return (
                            <div className="slide" id={slide.id+''}>
                                <img src={require(`../../${slide.img}`)} alt="NFT" />
                                <h3>{slide.name}</h3>
                                <div className="slide-footer">
                                    <div className="left">
                                        <p className='current'>Current bid</p>
                                        <div className="price-flex">
                                            <img src={require('../images/ethereum.png')} alt="ethereum" />
                                            <p className='price'>{slide.price}</p>
                                        </div>
                                        
                                    </div>
                                    <NavLink onClick={() => window.scrollTo(0, 100)} to={'/card/' + slide.id}>PLACE BID</NavLink>
                                </div>
                            </div>
                        )
                    })}
                </div>
                <div className="flex-btn">
                    <div className="slide-btn">
                        <button className=' btn btn-1' onClick={btnLeft}>&lt;-</button>
                        <button className='btn btn-2' onClick={btnRight}>-&gt;</button>
                    </div>
                </div>
                
            </div>
        </article>
    )
}
