import { slideInfo } from "../arrays/slides";
import { useNavigate, useParams } from "react-router-dom";
import './styles/css/card.css';
import {useState} from "react";
import ModalTemplate from "../common_components/components/ModalTemplate";
import {useForm} from 'react-hook-form';
import './styles/css/placeBid.css'
  
interface IFormInputs {
    price: string,
    'price-select': string
}

export default function Card() {
    const navigate = useNavigate();
    const params = Number(useParams().idCard);
    const slide = slideInfo.find(x => x.id === params); // нужный объект
    const [modal, setModal] = useState(false);
    const methods = useForm<IFormInputs>();
    const {
        register,
        formState: {
            errors
        },
        handleSubmit
    } = methods;

    return (
    <>
        <div id="card" className="card">
            <button className="back" onClick={() => navigate(-1)}><img src={require('./images/back.png')} alt="<-" />Go back</button>
            <div className="place">
                <img src={require(`../${slide?.img}`)} alt="NFT"/>
                <div className="info">
                    <h2>{slide?.name}</h2>
                    <p className="descript">{slide?.descript}</p>
                    <div className="persons">
                        <div className="created person">
                            <img src={require(`../${slide?.createdImg}`)} alt="person created" />
                            <div className="info-person">
                                <p className="gh">Created by</p>
                                <h4 className="name-person">{slide?.created}</h4>
                            </div>
                        </div>
                        <div className="owned person">
                            <img src={require(`../${slide?.ownedImg}`)} alt="person owned" />
                            <div className="info-person">
                                <p className="gh">Owned by</p>
                                <h4 className="name-person">{slide?.owned}</h4>
                            </div>
                        </div>

                    </div>
                    <div className="info-bottom">
                        <div className="current">
                            <p>Current Bid</p>
                            <strong><img src={require('../common_images/ethereum.png')} alt="ethereum" />{slide?.price}</strong>
                        </div>
                        <div className="date">
                            <p>End in</p>
                            <strong>{slide?.time}</strong>
                        </div>
                    </div>
                    <button onClick={() => setModal(true)}><img src={require('./images/bid.png')} alt="bid" /> Place Bid</button>
                </div>
            </div>  
        </div>

        {modal && 
        <ModalTemplate onClose={() => setModal(false)}>
            <div className="place-bid">
                    <div className="person">
                        <img src={require(`../${slide?.ownedImg}`)} alt="person owned" />
                        <div className="info-person">
                            <p className="gh">Owned by</p>
                            <h4 className="name-person">{slide?.owned}</h4>
                        </div>
                    </div>
                    <div className="input-bid">
                        <h3>Your Bid</h3>
                        <div className="price">
                            <select {...register("price-select")} id="price-select">
                                    <option value="bth">BTH</option>
                                    <option value="eth">ETH</option>
                                    <option value="bnb">BNB</option>
                                    <option value="ltc">LTC</option>
                                    <option value="ern">ERN</option>
                            </select>
                            <input  {...register("price", {required: 'Input field must not be empty!'})}  step="0.0000001" min="0.0000001" autoComplete='off' type="number" placeholder= "0.00007 ETH" id="price" />
                        </div>
                        {errors?.price && 
                    <div className='error'> 
                        <img src={require('./images/cross.png')} alt="" />
                        <p>{errors?.price?.message}</p>
                    </div>}
                    </div>
                    <button onClick={() => setModal(false)}><img src={require('./images/bid.png')} alt="bid" /> Place Bid</button>
            </div>
        </ModalTemplate>}
    </>
    )
}