import { useState, useRef } from "react";
import './styles/css/chooseNFT.css'
import {useFormContext} from 'react-hook-form'

export default function FilePreviewer() {
  const [imagePreview, setImagePreview] = useState(null);
  const [videoPreview, setVideoPreview] = useState(null);

  const { register } = useFormContext();
  const filePicekerRef = useRef(null);

  function previewFile(e) {
    const reader = new FileReader();

    const selectedFile = e.target.files[0];
    if (selectedFile) {
      reader.readAsDataURL(selectedFile);
    }

    reader.onload = (readerEvent) => {
      console.log(readerEvent.target.result)
      if (selectedFile.type.includes("image")) {
        setImagePreview(readerEvent.target.result);
      } else if (selectedFile.type.includes("video")) {
        setVideoPreview(readerEvent.target.result);
      }
    };
  }
  function clearFiles() {
		setImagePreview(null);
		setVideoPreview(null);
	}

  return (
    <div>

      <div className="btn-container">
        <input {...register("avatar", {required: true})}  ref={filePicekerRef} accept="image/*, video/*" onChange={previewFile} type="file" hidden />
      </div>
	  

      <div className="preview">
	  	{(imagePreview || videoPreview) == null && 
      <div className="null">
        <img src={require('./images/picker.png')} alt="pick"/>
        <p>PNG, GIF, WEBP, JPEG. Max 1Gb.</p>
      </div>}
        {imagePreview != null && <img src={imagePreview} alt="nft" />}
        {videoPreview != null && <video controls src={videoPreview} alt='nft'></video>}
      </div>
      {(imagePreview || videoPreview) == null && 
                        <div className='error'> 
                            <p>Input field must not be empty!</p>
                        </div>}

      <div className="btns">
        <div className="upload-btn" onClick={() => filePicekerRef.current.click()}>
          Upload
        </div>
        {(imagePreview || videoPreview) && (
					  <div className="upload-btn" onClick={clearFiles}>
						  x
					</div>
				)}
        
      </div>
      
    </div>
  );
}
