
import { NavLink } from "react-router-dom";
import { collections } from "../../arrays/topCollections";
import '../styles/css/collection.css';

export default function Collection() {
    let newCollections = [...collections.slice(0, 4)];


    return (

        <article id="top-collection" className="top-collection">
            <h2 className="Top">Top Collection</h2>
            <table>
                
                <tr>
                    <th>Collection</th>
                    <th>Volume</th>
                    <th className="hide-in-phone">24h %</th>
                    <th className="hide-in-phone">Floor Price</th>
                    <th className="hide-in-phone">Owners</th>
                    <th className="hide-in-phone">Items</th>
                </tr>
                
                    {newCollections.map(collection => {
                        return (
                        <tr>
                            <td className="name">
                                <div>
                                    <img src={''+collection.img} alt="profile" />
                                    <div className="name-info">
                                        <p className="name">{collection.name}</p>
                                        <p className="by">by {collection.by}</p>
                                    </div>
                                </div>

                            </td>
                        <td id="volume" className="volume">
                           <div>
                                <div className="price">
                                    <img src={require('../images/ethereum.png')} alt="ethereum" />
                                    <p>{collection.volume}</p>
                                </div>
                                <p id="hide-element" className="hide-elem">+ {collection.procent}%</p>
                           </div>
                        </td>
                        <td className="procent">
                            <div>
                                <p>+ {collection.procent}%</p>
                            </div>
                        </td>
                        <td className="price">
                            <div>
                                <img src={require('../images/ethereum.png')} alt="ethereum" />
                                <p>{collection.price}</p>
                            </div>
                        </td>
                        <td className="owners">
                            <div>
                                <p>{collection.owners/1000}K</p>
                            </div>
                        </td>
                        <td className="items">
                            <div>
                                <p>{collection.items}</p>
                            </div>
                        </td>
                    </tr>
                        )
                    })
                }
            
            
            </table>
            <NavLink  onClick={() => window.scrollTo(0, 0)} className="stats-href" to="/stats">Explore All <img src={require('../images/arrow-collections.png')} alt="->" /></NavLink>
        </article>
    )
}