import { NavLink } from 'react-router-dom';
import creatorsList from '../arrays/creators.js';
import './styles/css/creators.css'

export default function CreatorsPage() {
    return(
        <article className="creators">
            <h2>Meet Our Great Creators</h2>
            <div className="creators-list">
               {creatorsList.map(creator => {
                return (
                    <NavLink onClick={() => window.scrollTo(0, 0)} to={'/creator/' + creator.id} className="creator">
                        <img src={require(`../${creator.backPhoto}`)} alt="bg" />
                        <div className="info">
                            <div className="profile-photo"> <img src={require(`../${creator.photo1}`)} alt="profile"  /> </div>
                            <h3>{creator.name}</h3>
                            <div className="info-hr">
                                <div className="col">
                                    <div className="var">
                                        <h5>{creator.items / 1000}K</h5>
                                    </div>
                                    <p>Items</p>
                                </div>
                                <div className="col">
                                    <div className="var">
                                        <img src={require('../common_images/ethereum.png')} alt="ethereum" />
                                        <h5>{creator.floor}</h5>
                                    </div>
                                    <p>Floor price</p>
                                </div>
                                <div className="col">
                                    <div className="var">
                                        <img src={require('../common_images/ethereum.png')} alt="ethereum" />
                                        <h5>{creator.traded}</h5>
                                    </div>
                                    <p>Traded</p>
                                </div>
                            </div>
                            
                            <div className="works">
                                <img src={require('../'+ creator.photo1)} alt="work1" />
                                <img src={require('../'+ creator.photo2)} alt="work2" />
                                <img src={require('../'+ creator.photo3)} alt="work3" />
                            </div>
                        </div>
                    </NavLink>
                )
               })}
            </div>
        </article>
    )
}