import { collections } from "../arrays/topCollections";
import './styles/css/stats.css';

import { useState } from "react";

interface propInterface {
    'img': typeof import("*.png");
    'name': string;
    'by': string;
    'volume': number;
    'procent': number;
    'price': number;
    'owners': number;
    'items': number;
}

export default function StatsPage() {
    const [count, setCount] = useState(1)


    function sortFn(prop: string) {
        collections.sort((a: propInterface, b: propInterface) => {
            return a[prop] < b[prop] ? 1 : -1;
        })
        setCount(prev => ++prev)

    }

    return(
        <article className="stats">
            <h2 className="Top">Leaderboard NFTs</h2>
            {count && 
            <table>
                <tr>
                    <th>Collection </th>
                    <th onClick={() => sortFn('volume')}>Volume ↓</th>
                    <th className="hide-in-phone" onClick={() => sortFn('procent')}>24h % ↓</th>
                    <th className="hide-in-phone" onClick={() => sortFn('price')}>Floor Price ↓</th>
                    <th className="hide-in-phone" onClick={() => sortFn('owners')}>Owners ↓</th>
                    <th className="hide-in-phone" onClick={() => sortFn('items')}>Items ↓</th>
                </tr>
            {collections.map(collection => {
                 return (
                     <tr className="userTop">
                        <td className="name">
                            <div>
                                <img src={collection.img} alt="profile" />
                                <div className="name-info">
                                    <p className="name">{collection.name}</p>
                                    <p className="by">by {collection.by}</p>
                                </div>
                            </div>

                        </td>
                    <td id="volume"  className="volume">
                            <div>
                                <div className="price">
                                    <img src={require('./images/ethereum.png')} alt="ethereum" />
                                    <p>{collection.volume}</p>
                                </div>
                                <p id="hide-element" className="hide-elem">+ {collection.procent}%</p>
                           </div>
                    </td>
                    <td className="procent">
                        <div>
                            <p>+ {collection.procent}%</p>
                        </div>
                    </td>
                    <td className="price">
                        <div>
                            <img src={require('./images/ethereum.png')} alt="ethereum" />
                            <p>{collection.price}</p>
                        </div>
                    </td>
                    <td className="owners">
                        <div>
                            <p>{collection.owners/1000}K</p>
                        </div>
                    </td>
                    <td className="items">
                        <div>
                            <p>{collection.items}</p>
                        </div>
                    </td>
                </tr>
                    )

            })}
            
        </table>}
        </article>
    )
}

