import React, {useEffect, useState} from 'react';
import Header from './common_components/components/Header';
import ModalTemplate from './common_components/components/ModalTemplate';
import LoginForm from './common_components/components/LoginForm';
import {valueUserData} from './common_components/components/LoginForm';
import Footer from './common_components/components/Footer';
import { Route, Routes } from 'react-router-dom';

import HomePage from './Home/HomePage';
import MePage from './AboutMe/MePage';
import StatsPage from './Stats/StatsPage';
import DiscoverPage from './Discover/DiscoverPage';
import Card from './productCard/Card';
import CreatorsPage from './Creators/CreatorsPage';
import CreatorProfile from './CreatorProfile/CreatorProfile';
import CreateNFT from './CreateNFT/CreateNFT';



// Когда данные приняты мы вызываем функцию onCreteLogin() в LoginForm которая создает пользователя и убирает конпку JOIN!



function App() {
  const [login, setLogin] = useState(localStorage.getItem('login') || '');
  const [modal, setModal] = useState(false);

  function FnLogin() {
    setModal(false)
    setLogin(valueUserData.login)
    window.location.reload()
  }
  useEffect(() => {
    const raw  = localStorage.getItem('login') || '';
    setLogin(raw)
  }, [])
  useEffect(() => {
    localStorage.setItem('login', login+'')
  }, [login]) 



  return (
    <>
      <Header login = {login ? true : false} onOpen={() => setModal(true)}/>

      {modal && 
      <ModalTemplate onClose={() => setModal(false)}>
        <LoginForm onCreteLogin = {() => FnLogin()} />
      </ModalTemplate>} 

      <main>
        <Routes>
          <Route path='/' element = { <HomePage/> }/>
          <Route path='/me' element = { <MePage/> }/>
          <Route path='/stats' element = { <StatsPage/> }/>
          <Route path='/discover' element = { <DiscoverPage /> }/>
          <Route path='/creators' element = { <CreatorsPage /> }/>
          <Route path='/sell' element = { <CreateNFT /> }/>

          <Route path='/creator/:idCreator' element = { <CreatorProfile /> } />
          <Route path='/card/:idCard' element = { <Card /> } />
        </Routes>
      </main>
      <Footer />
    </>
  )
}

export default App;
